/* ## Box Sizing
--------------------------------------------- */
html,
input[type="search"] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* ## Float Clearing
--------------------------------------------- */
.author-box,
.clearfix,
.entry,
.entry-content,
.footer-widgets,
.nav-primary,
.nav-secondary,
.pagination,
.site-container,
.site-footer,
.site-header,
.site-inner,
.sidebar .widget,
.wrap {
  clear: both;
}

.author-box:before, .author-box:after,
.clearfix:before,
.clearfix:after,
.entry:before,
.entry:after,
.entry-content:before,
.entry-content:after,
.footer-widgets:before,
.footer-widgets:after,
.nav-primary:before,
.nav-primary:after,
.nav-secondary:before,
.nav-secondary:after,
.pagination:before,
.pagination:after,
.site-container:before,
.site-container:after,
.site-footer:before,
.site-footer:after,
.site-header:before,
.site-header:after,
.site-inner:before,
.site-inner:after,
.sidebar .widget:before,
.sidebar .widget:after,
.wrap:before,
.wrap:after {
  clear: both;
  display: block;
  content: '';
}

/* ## Typography
--------------------------------------------- */
* {
  -webkit-tap-highlight-color: transparent;
}

::selection {
  background-color: #141e28;
  color: #ffffff;
}

html {
  font-size: 62.5%;
  max-width: 100vw;
  overflow-x: hidden;
  background-color: #141e28;
}

body {
  background-color: #f6f7f8;
  color: #141e28;
  font-family: "Hind", Helvetica, Arial, sans-serif;
  font-size: 18px;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  margin: 0;
  max-width: 100vw;
  overflow-x: hidden;
}

@media (min-width: 768px) {
  body {
    font-size: 2rem;
  }
}

body > div {
  font-size: 18px;
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  body > div {
    font-size: 2rem;
  }
}

p {
  margin: 0 0 1.382em;
  padding: 0;
}

a {
  color: #141e28;
  word-wrap: break-word;
  -webkit-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  -moz-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  -o-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
}

a:hover {
  color: #556677;
}

ol,
ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

hr {
  border: 0;
  border-collapse: collapse;
  border-bottom: 2px solid #f0f1f2;
  border-bottom: 0.2rem solid #f0f1f2;
  clear: both;
  padding: 1em 0 0;
  margin: 0 0 2em;
}

b,
strong {
  font-weight: 600;
}

blockquote,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 1em 0;
  padding: 0 1em;
}

/* ## Headings
--------------------------------------------- */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-weight: 600;
  line-height: 1.2;
  margin: 0 0 0.382em;
}

h1 {
  font-size: 1.8em;
}

@media (min-width: 768px) {
  h1 {
    font-size: 2.2em;
  }
}

h2 {
  font-size: 1.4em;
}

@media (min-width: 768px) {
  h2 {
    font-size: 1.5em;
  }
}

h3 {
  font-size: 1.3em;
}

@media (min-width: 768px) {
  h3 {
    font-size: 1.4em;
  }
}

h4 {
  font-size: 1.2em;
}

@media (min-width: 768px) {
  h4 {
    font-size: 1.3em;
  }
}

h5 {
  font-size: 1.1em;
}

@media (min-width: 768px) {
  h5 {
    font-size: 1.2em;
  }
}

h6 {
  font-size: 1.05em;
}

@media (min-width: 768px) {
  h6 {
    font-size: 1.1em;
  }
}

/* ## Forms
--------------------------------------------- */
input,
select,
textarea {
  background-color: #ffffff;
  border: 2px solid #f0f1f2;
  border: 0.2rem solid #f0f1f2;
  border-radius: 0.4rem;
  padding: 0.5em 1em;
  width: 100%;
  line-height: 2;
  font-size: 18px;
  font-size: 1.8rem;
}

input:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: #556677;
}

:focus {
  outline: 2px solid #f0f1f2;
  outline: 0.2rem solid #f0f1f2;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
  width: auto;
}

input[type="search"] {
  -webkit-appearance: none;
}

::-moz-placeholder {
  color: #556677;
  opacity: 1;
}

::-webkit-input-placeholder {
  color: #556677;
}

.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  background-color: #141e28;
  border: 0;
  border-radius: 0.4rem;
  color: #ffffff;
  cursor: pointer;
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-size: 1.6rem;
  font-weight: 700;
  padding: 1em 2em;
  text-decoration: none;
  display: inline-block;
  width: auto;
  height: auto;
  line-height: 1;
  box-shadow: inset 0 0 0 9rem rgba(255, 255, 255, 0), 0 0 0 -1rem rgba(20, 30, 40, 0);
  -webkit-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  -moz-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  -o-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
}

.button:hover,
button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
  color: #ffffff;
  box-shadow: inset 0 0 0 9rem rgba(255, 255, 255, 0.1), 0 1.25rem 1rem -0.5rem rgba(20, 30, 40, 0.25);
  -webkit-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  -moz-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  -o-transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
  transition: all 0.75s cubic-bezier(0, 1, 0.5, 1);
}

.button.accent,
button.accent,
input[type="button"].accent,
input[type="reset"].accent,
input[type="submit"].accent {
  background-color: #fb2056;
}

.button.small,
button.small,
input[type="button"].small,
input[type="reset"].small,
input[type="submit"].small {
  font-size: 12px;
  font-size: 1.2rem;
}

.button:disabled, .button:disabled:hover,
button:disabled,
button:disabled:hover,
input[type="button"]:disabled,
input[type="button"]:disabled:hover,
input[type="reset"]:disabled,
input[type="reset"]:disabled:hover,
input[type="submit"]:disabled,
input[type="submit"]:disabled:hover {
  background-color: #f6f7f8;
  border-width: 0;
  color: #697886;
  cursor: not-allowed;
}

.button + button,
.button + .button,
button + button,
button + .button,
input[type="button"] + button,
input[type="button"] + .button,
input[type="reset"] + button,
input[type="reset"] + .button,
input[type="submit"] + button,
input[type="submit"] + .button {
  margin-top: 1em;
  clear: both;
}

@media (min-width: 512px) {
  .button + button,
  .button + .button,
  button + button,
  button + .button,
  input[type="button"] + button,
  input[type="button"] + .button,
  input[type="reset"] + button,
  input[type="reset"] + .button,
  input[type="submit"] + button,
  input[type="submit"] + .button {
    margin-top: 0;
    clear: none;
  }
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button {
  display: none;
}

fieldset {
  margin-bottom: 1em;
}

/* ## Objects
--------------------------------------------- */
embed,
iframe,
img,
object,
video,
.wp-caption,
.wp-video-shortcode {
  max-width: 100%;
  display: block;
}

img {
  height: auto;
}

figure {
  margin: 0;
}

video {
  object-fit: cover;
}

.wp-video-shortcode {
  width: auto !important;
}

/* ## Tables
--------------------------------------------- */
table {
  background-color: #ffffff;
  border: 2px solid #f0f1f2;
  border: 0.2rem solid #f0f1f2;
  border-collapse: collapse;
  border-spacing: 0;
  line-height: 1;
  margin-bottom: 40px;
  margin-bottom: 4rem;
  width: 100%;
}

tbody {
  border-bottom: 2px solid #f0f1f2;
  border-bottom: 0.2rem solid #f0f1f2;
}

td,
th {
  text-align: left;
  padding: 0.618em;
  border-top: 2px solid #f0f1f2;
  border-top: 0.2rem solid #f0f1f2;
  border-right: 2px solid #f0f1f2;
  border-right: 0.2rem solid #f0f1f2;
}

/* ## Accessibility
--------------------------------------------- */
.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut,
.wp-custom-logo .site-title,
.wp-custom-logo .site-description {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  height: 0.1rem;
  width: 1px;
  width: 0.1rem;
  border: 0;
  overflow: hidden;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus {
  clip: auto !important;
  height: auto;
  width: auto;
  display: block;
  padding: 10px 20px;
  padding: 1rem 2rem;
  z-index: 100000;
  text-decoration: none;
  box-shadow: 0 0 0.2rem 0.2rem rgba(20, 30, 40, 0.1);
}

.more-link {
  position: relative;
  font-style: normal;
}

.genesis-skip-link {
  margin: 0;
}

.genesis-skip-link li {
  height: 0;
  width: 0;
  list-style: none;
}

/* ## Site Containers
--------------------------------------------- */
.site-inner {
  background-color: #ffffff;
}

.wrap {
  margin: 0 auto;
  padding-left: 5%;
  padding-right: 5%;
}

@media (min-width: 768px) {
  .wrap {
    width: 90%;
    max-width: 1280px;
    padding: 0;
  }
}

.wrap .wrap {
  max-width: 100%;
  width: auto;
  padding: 0;
}

.content-sidebar-wrap {
  max-width: 1280px;
  margin: 0 auto;
  clear: both;
  padding: 10vw 5vw;
  word-wrap: break-word;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

@media (min-width: 768px) {
  .content-sidebar-wrap {
    width: 90%;
    max-width: 1280px;
    padding: 6rem 0;
  }
}

@media (min-width: 896px) {
  .content-sidebar-wrap {
    padding: 8rem 0;
    flex-direction: row;
    justify-content: space-between;
  }
  .content-sidebar-wrap:before, .content-sidebar-wrap:after {
    position: absolute;
  }
  .sidebar-content .content-sidebar-wrap {
    flex-direction: row-reverse;
  }
}

.page-builder .content-sidebar-wrap {
  padding: 0;
  border-top: 2px solid #f0f1f2;
  border-top: 0.2rem solid #f0f1f2;
  border-bottom: 2px solid #f0f1f2;
  border-bottom: 0.2rem solid #f0f1f2;
}

/* ## Content Widths and Positions
--------------------------------------------- */
@media (min-width: 896px) {
  .content {
    width: 768px;
    margin-left: auto;
    margin-right: auto;
  }
  .sidebar-primary {
    width: 25%;
  }
  .sidebar-content .content,
  .content-sidebar .content {
    width: calc(75% - 4rem);
    margin-left: 0;
    margin-right: 0;
  }
  .full-width-content .content {
    width: 100%;
  }
}

/* ## Column Classes (including fifths)
--------------------------------------------- */
